@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Oswald-Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Oswald-Bold.woff2") format("woff2"), url("../fonts/Oswald-Bold.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Oswald-Medium.woff") format("woff");
}
/* cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans Regular"), local("OpenSans-Regular"), url(https://fonts.gstatic.com/s/opensans/v17/mem8YaGs126MiZpBA-UFUZ0bbck.woff2) format("woff2");
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  font-family: "Open Sans", "Arial", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-style: normal;
  position: relative;
}

section {
  width: 100%;
  display: block;
  padding-top: 1px;
}

img {
  max-width: 100%;
  height: auto;
}

p {
  margin-top: 15px;
  margin-bottom: 15px;
  color: #999999;
}

h1, .h1 {
  font-family: "Oswald";
  font-size: 57px;
  font-weight: 500;
  line-height: 84px;
  margin: 0px 0 80px;
  text-transform: uppercase;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.vertical-line-grid {
  max-width: 1270px;
  position: fixed;
  margin: 0 auto;
  border-left: 1px solid #E4E4E5;
  border-right: 1px solid #E4E4E5;
  height: 100%;
  left: 0;
  right: 0;
  z-index: 2;
  pointer-events: none;
}
.vertical-line-grid:after, .vertical-line-grid:before, .vertical-line-grid__inner:after {
  content: "";
  width: 1px;
  height: 100%;
  background-color: #E4E4E5;
  position: absolute;
}
.vertical-line-grid:after {
  left: 25%;
}
.vertical-line-grid:before {
  left: 50%;
}
.vertical-line-grid__inner:after {
  left: 75%;
}
@media (max-width: 992px) {
  .vertical-line-grid:before {
    display: none;
  }
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.nowrap {
  white-space: nowrap !important;
}

.text-center {
  text-align: center !important;
}

.container {
  max-width: 1310px;
  margin: 0 auto;
  padding: 0 15px;
}

.title, h2, .h2 {
  font-family: "Oswald";
  font-size: 36px;
  line-height: 53px;
  font-weight: 500;
  text-transform: uppercase;
}

.subtitle {
  font-family: "Oswald";
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  text-transform: uppercase;
}

h4, .h4 {
  font-family: "Oswald";
  font-weight: 500;
  text-transform: uppercase;
  font-size: 21px;
  line-height: 31px;
  margin-bottom: 6px;
  margin-top: 0;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.flex-col {
  flex: 0 0 50%;
  max-width: 50%;
  padding-right: 15px;
  padding-left: 15px;
}

.form-control {
  display: block;
  width: 100%;
  height: 55px;
  padding: 6px 12px 6px 20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #4f4f4f;
  border: 0;
  background-color: #fff;
  border: 2px solid #ffffff;
}

.form-control::-webkit-input-placeholder { /* Chrome */
  color: #999999;
}

.form-control:-ms-input-placeholder { /* IE 10+ */
  color: #999999;
}

.form-control::-moz-placeholder { /* Firefox 19+ */
  color: #999999;
  opacity: 1;
}

.form-control:-moz-placeholder { /* Firefox 4 - 18 */
  color: #999999;
  opacity: 1;
}

.form-control--success {
  border: 2px solid #27AE60;
}

.form-control--error {
  border: 2px solid #F7654A;
}

.form-control:focus {
  border: 2px solid #404149;
  outline: none;
}

.form-field {
  border: 1px solid transparent;
}
.form-field--success {
  border: 1px solid #DDEFE6;
}
.form-field--error {
  border: 1px solid #EB5757;
  position: relative;
}
.form-field--error input[type=text],
.form-field--error input[type=password] {
  color: #EB5757;
}
.form-field--error:after {
  content: "*";
  color: #eb5757;
  position: absolute;
  top: 2px;
  bottom: 0;
  left: 11px;
}

.form-row {
  margin-bottom: 13px;
}

.button-up-wrapper {
  display: none;
  position: fixed;
  bottom: 238px;
  left: 0;
  right: 0;
  margin: 0 auto;
  max-width: 1280px;
}

.button-up {
  height: 60px;
  width: 60px;
  border: none;
  background-image: url("../img/ico-scroll-top.svg");
  background-size: 100%;
  background-color: transparent;
  background-repeat: no-repeat;
  border-radius: 100%;
  color: transparent;
  margin-right: 56px;
  position: absolute;
  right: 0;
  cursor: pointer;
}
.button-up:focus {
  outline: none;
}

@media (max-width: 1199px) {
  .h1, h1 {
    font-size: 54px;
    line-height: 80px;
  }
}
@media (max-width: 767px) {
  .h4, h4 {
    font-size: 14px;
    line-height: 21px;
  }
  p {
    font-size: 14px;
    line-height: 19px;
  }
  .form-row {
    margin-bottom: 9px;
  }
  .form-control {
    height: 40px;
    font-size: 12px;
    line-height: 16px;
  }
  .button-up {
    width: 40px;
    height: 40px;
    margin-right: 15px;
  }
  .button-up-wrapper {
    bottom: 120px;
  }
}
.btn {
  display: inline-block;
  background-color: #F7654A;
  color: #ffffff;
  text-decoration: none;
  font-size: 16px;
  padding: 20px 40px;
  border: none;
  transition: background-color 0.2s;
  cursor: pointer;
}
.btn:hover {
  background-color: #6788CB;
}
.btn:active, .btn:focus {
  outline: 0;
  background-color: #7E6792;
}

@media (max-width: 767px) {
  .btn {
    font-size: 12px;
    line-height: 16px;
    padding: 12px 40px;
  }
}
.section-title {
  font-family: "Oswald";
  font-size: 36px;
  line-height: 53px;
  font-weight: 500;
  text-transform: uppercase;
  margin-top: 80px;
  text-align: center;
  margin: 100px auto 80px auto;
  max-width: 730px;
  color: #404149;
}

@media (max-width: 1024px) {
  .section-title {
    margin-top: 35px;
    margin-bottom: 35px;
    font-size: 22px;
    line-height: 33px;
  }
}
.text-block {
  padding-bottom: 70px;
}

.text-block__content {
  max-width: 770px;
  margin: 0 auto;
}

.text-block__section-title {
  margin-top: 90px;
  margin-bottom: 50px;
}

.burger {
  display: none;
}

@media (max-width: 1024px) {
  .burger {
    padding: 15px;
    cursor: pointer;
    position: relative;
    display: inline-block;
    z-index: 5;
  }
  .burger__line {
    height: 1.6px;
    background-color: #ffffff;
    border-radius: 100px;
    position: absolute;
    left: 7px;
    right: 7px;
  }
  .burger__line:before, .burger__line:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: -6px;
    height: 1.6px;
    background-color: #ffffff;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -ms-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms;
  }
  .burger__line:before {
    top: auto;
    bottom: -6px;
  }
  .burger.on .burger__line {
    background-color: transparent;
    left: 5px;
    right: 5px;
    top: 14px;
  }
  .burger.on .burger__line:before {
    top: 50%;
    transform: rotate(-45deg);
  }
  .burger.on .burger__line:after {
    top: 50%;
    transform: rotate(45deg);
  }
}
.logo {
  font-size: 16px;
  line-height: 26px;
  color: #ffffff;
  font-family: "Oswald";
  font-weight: 400;
  padding-right: 10px;
}
.logo__name {
  text-transform: uppercase;
}

@media (max-width: 767px) {
  .logo {
    font-size: 12px;
    line-height: 16px;
    padding-right: 0;
  }
}
.form-converce {
  background: rgba(0, 0, 0, 0.5);
  width: 570px;
  height: 570px;
  border-radius: 100%;
  padding-top: 95px;
}

.form-converce__title {
  color: #ffffff;
  text-align: center;
}

.form-converce__subtitle {
  font-family: "Oswald";
  color: #F7654A;
  text-align: center;
  margin-bottom: 32px;
  font-size: 36px;
  font-weight: 500;
  line-height: 53px;
  text-transform: uppercase;
}

.form-converce__form-control {
  width: 298px;
  margin: 0 auto;
}

.form-converce__btn {
  display: block;
