}
.menu-top {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.menu-top__link {
  color: #ffffff;
  text-decoration: none;
  font-size: 14px;
  line-height: 16px;
  padding: 10px 16px;
  border: 1px solid transparent;
  transition: 0.2s all;
}
.menu-top__link:hover, .menu-top__link.is-active {
  border: 1px solid #ffffff;
}

@media (max-width: 1199px) {
  .menu-top {
    flex-shrink: 0;
  }
  .menu-top__link {
    padding: 10px 12px;
  }
}
@media (max-width: 1024px) {
  .menu-top {
    flex-direction: column;
    position: absolute;
    top: 53px;
    left: 35px;
  }
  .menu-top__link {
    font-size: 14px;
    line-height: 16px;
    padding: 5px 0px;
    display: block;
  }
  .menu-top__link:hover,
.menu-top__link.is-active {
