  }
}
.navbar {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;
  transition: background 0.2s;
}

.navbar.show {
  background-color: rgba(37, 37, 37, 0.7);
  position: fixed;
}

.navbar.show.on {
  background-color: transparent;
}

.navbar__burger {
  display: none;
}

.navbar__flex {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 64px;
}

.navbar__logo {
  transition: opacity 0.2s;
}

@media (max-width: 1024px) {
  .navbar__container {
    padding-left: 35px;
    padding-right: 35px;
  }
  .navbar__burger {
    display: block;
    position: absolute;
    left: 28px;
    top: 9px;
  }
  .navbar__logo {
    padding-right: 0;
  }
  .navbar__flex {
    margin-right: 0;
    flex-direction: row-reverse;
  }
  .navbar__menu-top {
    opacity: 0;
    visibility: hidden;
  }
  .navbar.on .navbar__menu-top {
    opacity: 1;
    visibility: visible;
  }
  .navbar.on .navbar__logo {
    opacity: 0;
    visibility: hidden;
  }
}
@media (max-width: 767px) {
  .navbar {
    justify-content: space-between;
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.header {
  background-image: url("../img/vendor/bg-header.jpg");
  background-size: cover;
}

.header__bg {
  min-height: 725px;
  padding: 225px 20px 95px 64px;
  position: relative;
}
.header__bg:after {
  content: "";
  position: absolute;
  background: rgba(37, 37, 37, 0.5);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
}
.header__bg:before {
  content: "";
  position: absolute;
  background-image: url(../img/vendor/bg-header-home.png);
  background-size: cover;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
}

.header__z-index {
  position: relative;
  z-index: 1;
}

.header__offer {
  display: flex;
  transition: opacity 0.2s;
}

.header__label {
  color: #ffffff;
  margin-bottom: 55px;
}

.header__h1 {
  margin-bottom: 23px;
}

.header__descr {
  font-family: "Oswald";
  color: #ffffff;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 27px;
}

.header__left {
  max-width: 688px;
}

.header__right {
  flex-grow: 1;
  margin-top: -95px;
  margin-right: 10px;
}

@media (max-width: 1199px) {
  .header__bg {
    padding-left: 32px;
  }
}
@media (max-width: 1024px) {
  .header__container {
    padding: 0;
  }
  .header__bg {
    padding-top: 150px;
    padding-left: 35px;
    padding-right: 35px;
  }
  .header__bg:after {
    left: 20px;
    right: 20px;
  }
  .header.on .header__bg:after {
    position: fixed;
    z-index: 1;
  }
  .header.on .header__offer {
    opacity: 0;
    visibility: hidden;
  }
  .header__h1 {
    text-align: center;
  }
  .header__descr {
    text-align: center;
  }
  .header__label {
    text-align: center;
  }
  .header__offer {
    flex-direction: column;
    align-items: center;
  }
  .header__right {
    margin-top: 60px;
  }
}
@media (max-width: 767px) {
  .header__offer {
    align-items: flex-start;
  }
  .header__h1 {
    font-size: 24px;
    line-height: 36px;
    text-align: left;
    margin-bottom: 10px;
  }
  .header__descr,
.header__label {
    text-align: left;
  }
  .header__label {
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 15px;
